/*
font-family: 'Open Sans', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Raleway', sans-serif;
*/

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*:focus,
*:hover {
  outline: none;
}

:root {
  --color-primary: rgb(0, 156, 234);
  --color-primary-light: rgba(0, 156, 234, 0.2);
  --color-primary-dark: #008bd1;
  --color-secondary: rgba(0, 0, 0 0.3);
  --color-secondary-dark: rgba(0, 0, 0, 0.4);
  --color-black: #000;
  --color-danger: rgb(220, 53, 69);
  --color-danger-light: rgb(255, 69, 0);

  --color-input: #495057;

  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  color: rgba(68, 68, 68, 1);
  background-color: #f7f8f9;
}

code {
  font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Raleway", sans-serif;
}

a {
  color: #009cea;
}

a:hover,
a:focus {
  color: #1eb4ff;
  text-decoration: none;
}

/* container width  */
.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* buttons */
button {
  font-family: "Raleway", sans-serif;
  display: inline-block;
  border: none;
  outline: none;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 3px;
}

button:focus {
  border: none;
  outline: none;
  text-decoration: none;
}

/* input fields  */
input[type="text"],
input[type="date"],
textarea {
  height: 36px;
  font-size: 0.75rem;
  font-weight: 400;
  padding: 6px 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  color: var(--color-input);
  transition: all 0.3s ease-in-out;
  outline: none;
  outline-style: none;
}

input[type="text"]:focus,
input[type="date"]:focus,
textarea {
  border: 1px solid rgba(0, 123, 255, 0.5);
}

select {
  display: block;
  height: 36px;
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--color-input);
  line-height: 1.3;
  padding: 6px 15px;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
}
select::-ms-expand {
  display: none;
}
select:hover {
  border-color: #888;
}
select:focus {
  border: 1px solid rgba(0, 123, 255, 0.5);
  outline: none;
}
select option {
  font-weight: 400;
}

form label {
  font-size: 0.8rem;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0px;
}

/* table  */
th {
  font-size: 0.75em;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 560;
}

td {
  font-size: 0.85em;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 560;
  padding: 0px 0 0 0;
}

tr {
  z-index: 100;
}

/************************** data table ********************************/

.rdt_Table{
  min-width: 1170px !important;
  overflow-x: auto !important;
}

.rdt_TableHeader{
  display: none !important;
}


/**************************** media quires ********************************/
@media(max-width: 1480px) {
  .container{
    max-width: 1000px;
  }

  .rdt_Table{
    min-width: 1140px !important;
  }
}